/**
 * Nomie API Client
 *
 * This modules is a warpper for the nomie api.
 * Alling us to capture encrypted logs from nomieapi.com
 *\\
 */
import KeyLocker from "./key-locker/key-locker";
import { globalplugin } from '../store/stores';
import tick from "./tick";
let plugin;
const unsubscribe2 = globalplugin.subscribe((value) => plugin = value);
const API_CLI_CONFIG = "napi-cli-config";
export default class NomieAPICli {
    constructor(options) {
        this.listeners = []; // Prep listeners
        options = options || {}; // any options?
        this.domain = options.domain; // set domain if there.
        this.ready = false; // is it ready?
        this.keyLocker = new KeyLocker();
    }
    // initialie the API
    async init() {
        try {
            // Get configuration from Storage
            let config = JSON.parse(localStorage.getItem(API_CLI_CONFIG));
            await tick(500);
            if (config) {
                // Set Keylocker API Private Keys
                this.keyLocker.apiKey = config.apiKey || config.api_key;
                this.keyLocker.privateKey = config.privateKey || config.private_key;
                if (config.domain) {
                    this.domain = config.domain;
                }
                // we're ready!
                this.ready = true;
                this.fireReady(); // !fire off ready to listeners
            }
            else {
                // Not registered 
                this.ready = true;
                // Fire anyways - so we can let user register 
                this.fireReady();
            }
        }
        catch (e) {
            // Something bad has happened 
            //alert(`Something wrong has happened. Check the console for details. Msg: ${e.message}`);
            plugin.alert('ERROR', `Something wrong has happened. Check the console for details. Msg: ${e.message}`);
            console.error('API-Cli init error', e);
        }
    }
    /**
     * onReady
    * fires when this is ready
     * @param {*} func
     */
    onReady(func) {
        return this.ready ? func(this) : this.listeners.push(func);
    }
    /**
     * Private Fire Ready
     */
    fireReady() {
        this.listeners.forEach((func) => {
            func(this);
        });
    }
    baseUrl() {
        return this.domain.search("localhost") > -1 ? `http://${this.domain}` : `https://${this.domain}`;
    }
    isRegistered() {
        return this.keyLocker.apiKey && this.keyLocker.privateKey ? true : false;
    }
    unRegister() {
        this.keyLocker.privateKey = undefined;
        this.keyLocker.apiKey = undefined;
    }
    /**
     * Post to the API
     * @param {String} url
     * @param {Object} payload
     * @param {array} additionalHeaders
     */
    async post(url, payload, additionalHeaders) {
        var _a;
        // Set headers for json
        let headers = {
            "Content-Type": "application/json",
        };
        // Setup Calling URL
        let callingURL = `${this.baseUrl()}${url}`;
        // Mash headers and  parameter headers together .
        let finalHeaders = Object.assign(Object.assign({}, headers), additionalHeaders);
        // Call The service
        let results = await fetch(callingURL, {
            method: "POST",
            headers: finalHeaders,
            body: JSON.stringify(payload),
        });
        // Get Payload back
        let cbPayload = await results.json();
        // The key is no longer valid
        if (cbPayload.results.destroy) {
            throw new Error('Server is saying that is an invalid api/private key combo.');
            // await this.clearConfig();
        }
        if (results.status !== 400 && !cbPayload.success) {
            throw new Error(`API Server returned code: ${((_a = cbPayload === null || cbPayload === void 0 ? void 0 : cbPayload.results) === null || _a === void 0 ? void 0 : _a.message) || `Error Code: ${results.status}`}`);
        }
        return cbPayload;
    }
    /**
     * Test API
     * Test Vailidity of a Private Key API Combo
     */
    async test(apiKey, privateKey) {
        let success;
        this.keyLocker.apiKey = apiKey;
        this.keyLocker.privateKey = privateKey;
        let response = await this.post("/logs", { key: this.keyLocker.asAuth }, this.keyLocker.asAuth);
        success = response.success;
        return success;
    }
    /**
     * Test and Save
     * This will test, then save the API config to storage
     */
    async testAndSave(apiKey, privateKey) {
        let success = await this.test(apiKey, privateKey);
        if (success) {
            await this.saveKeys(apiKey, privateKey);
        }
        return success;
    }
    /**
     * Get Logs from API
     */
    async getLogs() {
        return await this.logs();
    }
    // Get Logs
    async logs() {
        const payload = await this.post("/logs", { key: this.keyLocker.asAuth }, this.keyLocker.asAuth);
        if (payload.success) {
            return payload.results;
        }
        else {
            throw new Error(payload.results.message);
        }
    }
    /**
     * For get this configuration
     */
    async forget() {
        return await this.clearConfig();
    }
    /**
     * Empty slots
     * This will clear out the slots of any remaining API logs on the server
     */
    async clear() {
        let payload = await this.post("/clear", { key: this.keyLocker.asAuth }, this.keyLocker.asAuth);
        if (payload.success) {
            return payload.results;
        }
        else {
            throw new Error(payload.results.message);
        }
    }
    /**
     * Forget this API
     * Clear Storage Config
     */
    async clearConfig() {
        localStorage.removeItem(API_CLI_CONFIG);
        //await tick(500);
        this.fireReady();
    }
    /**
     * Set Locker Config
     * @param config
     */
    async setConfig(config) {
        try {
            this.keyLocker.apiKey = config.apiKey || this.keyLocker.apiKey;
            this.keyLocker.privateKey = config.privateKey || this.keyLocker.privateKey;
            let configuration = this.keyLocker.asObject;
            configuration.domain = this.domain;
            localStorage.setItem(API_CLI_CONFIG, JSON.stringify(configuration));
        }
        catch (e) {
            //alert(e.message);
            plugin.alert('ERROR', e.message);
            console.error("Set Config error", e, config);
        }
    }
    /**
     * Save Keys to Storage
     * @param apiKey
     * @param privateKey
     */
    async saveKeys(apiKey, privateKey) {
        this.setConfig({ apiKey: apiKey, privateKey: privateKey });
        return true;
    }
    /**
     * Register for an API Key
     */
    async register() {
        let payload = await this.post("/register");
        if (payload.success) {
            this.keyLocker.apiKey = payload.results.apiKey;
            this.keyLocker.privateKey = payload.results.privateKey;
            this.setConfig({
                apiKey: payload.results.apiKey,
                privateKey: payload.results.privateKey,
            });
            return this;
        }
        else {
            throw new Error(payload.results.message);
        }
    }
    /**
     *
     * Unregister
     * Unregisters and removes the Current API Config
     *
     */
    async destory() {
        let payload = await this.post("/unregister", { key: this.keyLocker.asAuth }, this.keyLocker.asAuth);
        if (payload.success) {
            return true;
        }
        else {
            throw new Error(payload.results.message);
        }
    }
}
